@import "./styles/example.scss"; // import the file from our styles so it will be bundled

#main {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 550px; 
}

h3 {
    font-family: Helvetica;
    font-size: 175px;
    font-weight: 100;
}

h1 {
    text-align: center;
}

h2 {
    text-align: center;
}

body, label, text, a,li, #ladder, #graph, h2, h1 {
    background-color: #000;
    color: #fff;
    border: none;
    font-family: Helvetica;
}


div#input.showing {
    display: block;
    flex-direction: column;
    justify-content: center;
    width: 300px;
    height: 100%;
    // border:2px solid #fff;
}
div#input > form, div#ladder > form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: nowrap;
}

div#input > form > label,div#ladder > form > label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    margin: 10px;
    border: 1px solid white;
    font-family: Helvetica;
}
div#input > form > label > input[type=text], div#ladder > form > label > input[type=text] {
    // padding: 15px;
    justify-content: center;
    text-align: center;
    margin: auto;
    width: 150px;
    font-family: Helvetica;
}
div#input > form > label > input[type=checkbox]{
    margin: 10px;
    width: 10px;
    font-family: Helvetica;
}
div#input > form > label > input[type=submit], div#input > form > label > input[type=button], div#ladder > form > label > input[type=submit], div#ladder > form > label > input[type=button]{
    margin: auto;
    font-family: Helvetica;
}
input[type=text] {
    color: slategray;
}
input.clicked {
    color: black;
}

// div#ladder-and-graph {
//     height: 600px;
//     width: 1100px;
//     display: flex;
//     flex-direction: row;
//     justify-content: center;
// }

div#ladder.showing {
    display: block;
    height: 550px;
    width: 300px;
    // border:2px solid #fff;
}



div#graph.showing {
    display: block;
    position: relative;
    height: 100%;
    width: 900px;
    border:2px solid #fff;
    overflow: scroll;
    z-index: 1;
}

ul {
    list-style: none;
}

circle {
    stroke: #fff;
    stroke-width: 0;
    fill: white;
  }
  circle:hover {
    stroke: #fff;
    stroke-width: 2px;
    fill: red;
  }
  
.link {
    fill: none;
    stroke: #fff;
}

text,h1 {
    text-decoration: none;
    color: white;
    fill: white;
    font-family:Helvetica;
    font-weight: 400;
    stroke-width: 0%;
    border: none;
    // z-index: 1;
}
text:hover {
    text-decoration: none;
    fill: yellow;
    font-family: Helvetica;
    font-size: 25px;
    font-weight: bold;
    text-shadow: red 2px 0px 1px;
}

text.shortLadder {
    // text-decoration: none;
    fill: red;
    // z-index: 2;
    font-family: Helvetica;
    font-size: 30px;
    font-weight: bold;
    text-shadow: blue 2px 0px 2px;
}
text.shortLadder:hover {
    // text-decoration: none;
    fill: yellow;
    // z-index: 2;
    font-family: Helvetica;
    font-size: 30px;
    font-weight: bold;
    text-shadow: red 2px 0px 2px;
}

path {
    opacity: 100%;
    stroke: white;
}
path:hover {
    stroke-width: 4px;
    stroke: red;
}

.hidden {
    display: none;
}

#left,#right {
    height: 200px;
}

div#currentLadder {
    position: absolute;
    z-index: 2;
    top: -40px;
}

div#return {
    position: absolute;
    z-index: 2;
    top: 560px;
    cursor: pointer;
}


h4 {
    font-family: Helvetica;
    font-weight: 100;
}


div#info.showing {
    position: absolute;
    background-color: #000;
    border: 3px solid #fff;
    width: 450px;
    height: 90%;
    top: 3%;
    left: 0;
    right: 0;
    padding: 12px;
    margin-left: auto;
    margin-right: auto;
    overflow: scroll;
    z-index: 2;
    text-align: justify;
    text-justify: inter-word;
}

div#popup.showing {
    position: absolute;
    background-color: #000;
    border: 3px solid #fff;
    width: 450px;
    min-height: 25%;
    max-height: 50%;
    top: 15%;
    left: 0;
    right: 0;
    padding: 5px;
    margin-left: auto;
    margin-right: auto;
    overflow: scroll;
}

div#popup.showing > p {
    padding-right: 35px;
    padding-left: 35px;
}

div#ladderPopUpButtons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
div#ladderPopUp.showing {
    position: absolute;
    background-color: #000;
    border: 3px solid #fff;
    width: 330px;
    max-height: 75%;
    top: 15%;
    left: 0;
    right: 0;
    padding: 5px;
    margin-left: auto;
    margin-right: auto;
    overflow: scroll;
    z-index: 2;
    p {
        text-align: center;
    }
}

div#images {
    position: absolute;
    top: 2%;
    right: 2%;
}

div#info.showing > p {
    padding: 10px;
}

text.word {
    font-size: 13px;
}

div#return2 {
    position: relative;
    z-index: 2;
    cursor: pointer;
    margin: 6px;
    text-align: center;
}

a.boldAnchor:hover {
    transform: scale(1.3);
    transition: .5s;
    cursor: pointer;

}
a.boldAnchor2:hover {
    transform: scale(1.1);
    transition: .5s;
    cursor: pointer;

}
a.rotate:hover {
    rotate: 360deg;
}

.rotate {
    -webkit-animation: rotate 2s linear infinite;
    animation: colorRotate .5s linear 0s infinite;
}
@keyframes colorRotate {
    from {
      color: #6666ff;
    }
    10% {
      color: #0099ff;
    }
    50% {
      color: #00ff00;
    }
    75% {
      color: #ff3399;
    }
    100% {
      color: #6666ff;
    }
}
div#info div#animation {
    height: 25px;
    width: 450px;
    transition: height 2s;
}
div#xAnchor {
    position: absolute;
    top: 0px;
    right: calc(50% - 251px);
    margin-left: auto;
    margin-right: auto;
    font-size: 70px;
    z-index: 3;

    a {
        background: none;
        cursor: pointer;
    }
}
div#xAnchorpopup {
    position: absolute;
    top: calc(11% + 5px);
    right: calc(50% - 244px);
    margin-left: auto;
    margin-right: auto;
    font-size: 70px;
    z-index: 3;

    a {
        background: none;
        cursor: pointer;
    }
}

form > p {
    width: 248px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 6px;
    font-size: 22px;
}